/* Container principal pantalla completa */


/* Container de todo el buscador */
#searchbox-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
}

#searchbox-container-vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    position: -webkit-sticky;
    position: sticky;
    top: 15%;
    margin: 0 auto;
    grid-column: 1;
}


    /* Container (solo ida, ida y vuelta) */
    .oneway-container {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        background-color: transparent;
        width: 100%;
        padding-left: 1rem;
    }   
        /* Botones (solo ida, ida y vuelta) */
        .oneway-container > button {
            padding: 0.2rem 1rem;
            border-radius: 20px 20px 0 0;
            border:none;
            cursor: pointer;
            text-transform: uppercase;
            font-family: Roboto, sans-serif;
            font-weight: 400;
            font-size: 0.7rem;
        }
            .oneway-container > button:active {
                -webkit-transform: translate3d(0px, 1px, 1px);
                        transform: translate3d(0px, 1px, 1px);
            }

        /* Solo ida por defecto seleccionado */
        .oneway-option-1 {
            background-color: #132C33;
            color: white;
}
        

    /* Container del formulario */
    .searchbox-form-container {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background-color: rgb(255,255,255,.7);
        border-radius: 15px;
    }
        
        /* CONTAINER FORMULARIO */
        .searchbox-form {
            display: grid;
            grid-template-columns: repeat(8,1fr);
            grid-template-rows: 1fr 0.1fr 2.5fr 1fr;
            align-items: center;
            justify-content: center;
            min-width: 600px;
            padding: 1rem;
            padding-top: 0;
        }
        .searchbox-form-vertical {
            display: grid;
            grid-template-columns: repeat(8,1fr);
            grid-template-rows: 1fr 0.1fr 2.5fr 0.5fr;
            align-items: center;
            justify-content: center;
            min-width: 250px;
            padding: 1rem;
            padding-top: 0;
            background-color: rgb(255,255,255);
            border-radius: 15px;
            
        }    
            /* CONTAINER (ESCALAS, PERSONAS, VUELOS CERCANOS) */
            #first-search-options {
                grid-column: span 8;
                grid-row: 1;
                display: flex;
                align-items: center;
                justify-content: space-around;
                font-family: Roboto, sans-serif;
                font-weight: 300;
                font-size: 0.8rem;
            }
            #first-search-options-vertical {
                grid-column: span 8;
                grid-row: 1;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                font-family: Roboto, sans-serif;
                font-weight: 300;
                font-size: 0.8rem;
                grid-gap: 0.5rem;
                gap: 0.5rem;
                padding-top: 0.5rem;
                padding-bottom: 0.5rem;
            }
                #first-search-options > label {
                    font-size: 0.8rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-width: -webkit-max-content;
                    min-width: max-content;
                }

                #first-search-options-vertical > label {
                    font-size: 0.8rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-width: -webkit-max-content;
                    min-width: max-content;
                }
                
                #first-search-options > label > input, #first-search-options-vertical > label > input {
                    margin-right: 5px;
                }

            
                
            /* LINEA DE SEPARACION ENTRE OPCIONES 1 Y 2 DE BUSQUEDA */
            #separation-line {
                grid-column: span 8;
                grid-row:2;
                background-color:rgb(19, 44, 51,.5);
                height: 2px;    
            }
            /* CONTAINER INPUTS DE BUSQUEDA */
            #second-search-options{
                grid-column: span 8;
                grid-row: 3;
                display: grid;
                grid-template-columns: repeat(4,1fr); 
                grid-gap: 0.5rem;
                padding: 0.5rem 0;
                padding-top: 0.6rem;     
            }

            #second-search-options-vertical{
                grid-column: span 8;
                grid-row: 3;
                display: grid;
                grid-template-columns: 1fr; 
                grid-gap: 0.5rem;
                padding: 0.5rem 0;
                padding-top: 0.6rem;     
            }
                #input-origin, #input-destination, 
                #input-departure-date, #input-return-date {
                    grid-column: span 2;
                    position: relative; 
                 
                    
                 
                }

                #input-origin .MuiAutocomplete-root, #input-destination .MuiAutocomplete-root {
                    box-shadow: none !important;
                    border: 1px solid rgb(0 ,0 ,0, 23%);
                    
                }

                #input-departure-date input, #input-return-date input {
                    padding: 1.1rem 0.7rem;
                    border:none;
                    min-width: -webkit-max-content;
                    min-width: max-content;
                    width: 100% !important;
                    font-family: Roboto, sans-serif;
                    font-size: 0.8rem;
                    font-weight: normal;
                    background-color: rgba(255, 255, 255, 0.5);
                    color: black;
                    border: 1px solid rgb(0 ,0 ,0, 23%);
                    border-bottom: 1px solid rgb(141, 140, 140);
                    transition: 0.5s;
                }
                #input-departure-date input:hover, #input-return-date input:hover:enabled {
                    border-bottom: 1px solid black;
                }
                    #input-return-date input:disabled {
                        background-color: rgb(184, 184, 184);  
                    }
                
            #search-button {
                grid-column: span 8;
                grid-row: 4;
                background-color: #132C33;
                color: white;
                padding:0.7rem;
                border: none;
                border-radius: 0 0 10px 10px;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                grid-gap: 0.5rem;
                gap: 0.5rem;
                box-shadow: -1px 1px 2px rgb(129, 129, 129) !important;
                font-family: Roboto, sans-serif;
                font-weight: 400;
                font-size: 1rem;
            }

            #search-button-vertical {
                grid-column: span 8;
                grid-row: 4;
                background-color: #132C33;
                color: white;
                padding:0.7rem;
                border: none;
                border-radius: 0 0 10px 10px;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                grid-gap: 0.5rem;
                gap: 0.5rem;
                box-shadow: -1px 1px 2px rgb(129, 129, 129) !important;
                font-family: Roboto, sans-serif;
                font-weight: 400;
                font-size: 1rem;
            }
                #search-button:active {
                    -webkit-transform: translate3d(0px, 1px, 1px);
                            transform: translate3d(0px, 1px, 1px);
                }
                #search-button:hover {
                    background-color: #0f1d20;
                }
            .react-datepicker-wrapper {
                width: 100%;
                background-color: white;
            }
            .react-datepicker-wrapper > div > input:hover {
                width: 100%;
                background-color: rgb(26, 25, 25);
            }
            
                


        

/* Container del select y del div dinamico para la seleccion de clase y persona */
#container-class-selector {
    position: relative;
}

/* Container selección clase y personas */
#class-adults-search {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    flex-direction: column;
    background-color: rgb(240,240,240,.9);
    border: 1px solid #132C33;
    border-left: 3px solid #132C33;
    border-bottom: 6px solid #132C33;
    border-radius: 8px;
    padding: 1rem;
    margin-top: 0.1rem;
    position: absolute;
    left: 0;
    right: 0;
    grid-gap: 2px;
    gap: 2px;
    z-index: 1003;
    -webkit-animation: effect-in 0.5s;
            animation: effect-in 0.5s;
}

@-webkit-keyframes effect-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes effect-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


/* Select */
#select-class-options {
    cursor: pointer;
    padding: 0.1rem 0.2rem;
    text-align: start;
    min-width: 160px;
    font-family: Roboto, sans-serif;
    font-weight: 300;
}

#class-adults-search label {
    font-weight: bold;
    font-size: 0.8rem;
}

#class-adults-search label span {
    font-weight: normal;
    font-size: 0.6rem;
}
#class-adults-search select {
    padding: 0.2rem 0.4rem;
    font-size: 0.8rem;
    font-family: Roboto, sans-serif;
}

/* Container de la selección de adultos y niños */
.adults-childs-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    grid-gap: 0.2rem;
    gap: 0.2rem;
}

/* Botones para incrementar y decrementar la cantidad de personas */
.adults-childs-container .button {
    padding: 0.1rem;
    cursor: pointer;
    background-color: #132C33;
    color:white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    width: 20px;
    height: 20px;
}

.adults-childs-container input {
    padding: 0.1rem;
    font-size: 0.8rem;
    font-family: Roboto, sans-serif;
    text-align: center;
    width: 60px;
}

/* Botón: 'Listo' para terminar la seleccion de clase-adultos-niños */
#finish-selection-button-class-adults {
    cursor: pointer;
    align-self: flex-end;
    font-size: 0.8rem;
    padding: 0.2rem;
    margin-top: 0.4rem;
    font-weight: 500;
    width: 100%;
    text-align: center;
    background-color: #132C33;
    border-radius: 15px;
    color: white;
}











