.restore-password-form{
    min-height: 150px;
    width: 350px;
    background-color: #ffffff;
    border-radius: 5px;
    border: 3px solid #132C33;
    position: absolute;
    z-index: 5000;
    left: calc(50% - 175px);
    top: calc(50% - 75px);
    transform: translate(-50%,-50%);
    text-align: center;
    padding: 20px !important;
  }
#standard-basic-email-label{
  z-index: 5000 !important;
}

.restore-password-form button {
  margin-top: 1rem;
}

.restore-password-form h3 {
  border-bottom: 1px solid black;
  padding: 0.5rem;
  font-size: 1rem;
  text-align: start;
}
.restore-password-form p {
  font-size: 0.7rem;
  padding: 0.5rem;
  text-align: start;
}